import { createRouter, createWebHistory } from "vue-router";
import Index from "../views/Index";
import IndexConquest from "../views/conquest/Index";
import Catalog from "../views/conquest/Catalog";
import Card from "../views/conquest/Card";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Index
  },
  //Conquest
  {
    path: "/Conquest/",
    name: "Conquest",
    component: IndexConquest
  },
  {
    path: "/Conquest/catalog",
    name: "Catalog",
    component: Catalog
  },
  {
    path: "/Conquest/card",
    name: "Card",
    component: Card
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
